.ec-payment__loading{width:100%;padding-top:4rem;text-align:center}.ec-payment__loading>div{width:6rem;height:6rem}.ec-payment__head{display:-webkit-box;display:flex;-webkit-box-pack:justify;justify-content:space-between}.ec-payment__icon{width:2em;text-align:center;margin-left:.25rem}.ec-payment__text{display:block;margin-top:.25rem;font-size:.9rem;color:var(--gray)}.ec-payment__badges{display:inline-block;text-transform:lowercase}.ec-payment__badges .badge{margin-right:.25rem}.ec-payment__stamp{max-width:100%;margin:.75rem auto 0}.ec-payment__intermediator{color:var(--gray)}.ec-payment__back{color:var(--gray);font-size:.85rem;padding:.15rem}.ec-payment__back:hover{color:var(--black)}.ec-payment__back:focus{outline:none}