.ec-checkout__steps{display:-webkit-box;display:flex;margin-bottom:.5rem}.ec-checkout__step{padding:.35rem;-webkit-box-flex:1;flex-grow:1;color:var(--primary-light);text-align:center;font-size:.85rem;position:relative;text-transform:uppercase}.ec-checkout__step>small{border-radius:50%;background:var(--light);width:1rem;display:inline-block;border-right:1px solid var(--primary)}.ec-checkout__step:not(:last-child):before{position:absolute;right:0;content:"/";color:var(--gray);font-weight:700}.ec-checkout__step--current{font-weight:700;color:var(--secondary);pointer-events:none}.ec-checkout__step--past,.ec-checkout__step--past:hover{color:var(--success)}.ec-checkout__step--disabled{color:var(--gray);pointer-events:none}.ec-checkout__step:hover{text-decoration:none}.ec-checkout__step:focus{outline:none}.ec-checkout__app{min-height:300px;max-width:90rem;margin:0 auto 2rem}.ec-checkout__info{background:var(--light)}@media (max-width:991.98px){.ec-checkout__info{margin-top:2.5rem}}@media (min-width:992px){.ec-checkout__info{border-left:.3rem solid var(--secondary);border-radius:.25rem 0 0 .25rem}}.ec-checkout__back{margin-top:2rem;text-align:right}@media (max-width:575.98px){.ec-checkout__back>.btn{display:block}}.ec-checkout__col{padding-top:1rem}.ec-checkout__shipping{margin-bottom:1.5rem}.ec-checkout__shipping-method{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-pack:justify;justify-content:space-between;margin-bottom:1rem;border-radius:.25rem;padding:.75rem 1rem;background:var(--light);border-left:3px solid var(--success)}.ec-checkout__shipping-method small{color:var(--gray)}.ec-checkout .ec-shipping__services{max-width:100%}.ec-checkout__overlay{background:var(--body-bg);position:absolute;width:100%;height:100%;top:0;left:0;z-index:-1}.ec-checkout__overlay.show{z-index:10;opacity:.5}.ec-checkout__summary{padding:1rem .5rem;display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;-webkit-box-pack:justify;justify-content:space-between;height:100%}.ec-checkout__discount{margin-bottom:1.5rem}.ec-checkout__user{margin-bottom:1rem}.ec-checkout__user small{color:var(--gray)}